
/* ------------------------------------------------------------------------------
	Header
-------------------------------------------------------------------------------*/
#header {
	position: fixed;
	width: 100%;
	z-index: 999;
	background: transparent;

	.header-content {
		margin: 0 auto;
		max-width: 1170px;
		padding: 60px 0;
		width: 100%;
		transition: padding 0.3s;
	}
	.logo {
		float: left;
		font-size:24px;
		font-weight:700;
		color: $black;
		text-decoration: none;
		text-transform: uppercase;
		letter-spacing: 7px;
	}
	&.fixed {
		background-color: rgba(44, 62, 80, 0.9);
		a {
			color: $white;
		}

		.header-content {
			border-bottom: 0;
			padding: 25px 0;
		}
		.nav-toggle {
			top: 18px;
			color: $white;
		}
	}
}


@media screen and (max-width: 768px) {
    #header {
			a {
					border-bottom: 0;
					color: $white;
					&:hover, &.active {
						border-bottom: 0;
						color: $orange;
					}
			}
        &.fixed {
            a {
								border-bottom: 0;
                color: $white;
								&:hover, &.active {
									border-bottom: 0;
	                color: $orange;
								}

                &.logo{
                	 color: $black;
                }
            }
        }
    }
}





/* ------------------------------------------------------------------------------
	Banner
-------------------------------------------------------------------------------*/
.banner {
	background-image: url(./img/backs/orange-back.jpg);
	background-position: center top;
	background-repeat: no-repeat;
	background-size: cover;
	min-height: 350px;
	height: 100%;
}

.banner-text {
	padding-top: 45%;

	h1 {
		color: $white;
		font-family: "Open Sans", Arial, sans-serif;
		font-size: 36px;
		font-weight: 700;
		text-transform: uppercase;
	}

	p {
		color: $white;
		font-size: 16px;
		font-weight: 400;
		line-height: 24px;
		margin-top: 30px;
		margin-bottom: 80px;
	}
}
